.shell {
  min-height: 100vh;
  background: radial-gradient(circle at 20% 20%, rgba(255, 0, 120, 0.12), transparent 30%),
    radial-gradient(circle at 80% 0%, rgba(0, 255, 200, 0.1), transparent 30%),
    #0f1116;
  color: #f2f2f2;
  font-family: 'Press Start 2P', 'Noto Sans JP', system-ui, -apple-system, sans-serif;
}

.main {
  padding: 112px 28px 80px;
  max-width: 1280px;
  margin: 0 auto;
}

.header {
  position: sticky;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 28px;
  background: rgba(8, 8, 12, 0.92);
  backdrop-filter: blur(6px);
  border-bottom: 2px solid #e23b44;
  max-width: 1280px;
  margin: 0 auto;
}

.logo {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 15px;
  letter-spacing: 1px;
}

.logo__dot {
  color: #00f0ff;
}

.logo__text {
  color: #f8e71c;
  text-shadow: 0 0 4px #f8e71c;
}

.nav {
  display: flex;
  align-items: center;
  gap: 16px;
}

.nav__link {
  color: #cfd8dc;
  text-decoration: none;
  padding: 10px 12px;
  border: 1px solid transparent;
  border-radius: 6px;
  transition: 0.2s ease;
}

.nav__link:hover {
  color: #ffffff;
  border-color: rgba(255, 255, 255, 0.2);
}

.nav__link.is-active {
  color: #0b0b0b;
  background: #e23b44;
  border-color: #e23b44;
  box-shadow: 0 0 0 2px #0b0b0b, 0 4px 0 #951d26;
}

.nav-toggle {
  display: none;
  flex-direction: column;
  gap: 4px;
  background: none;
  border: none;
  padding: 8px;
}

.nav-toggle span {
  width: 24px;
  height: 3px;
  background: #fff;
  display: block;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 14px 18px;
  border-radius: 8px;
  border: 2px solid #fff;
  text-decoration: none;
  font-size: 12px;
  letter-spacing: 0.5px;
  color: #0b0b0b;
  background: #f8e71c;
  box-shadow: 0 4px 0 #a89e11;
  transition: transform 0.12s ease, box-shadow 0.12s ease;
}

.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 0 #a89e11;
}

.btn:active {
  transform: translateY(1px);
  box-shadow: 0 2px 0 #a89e11;
}

.btn-primary {
  background: linear-gradient(90deg, #ff3b30, #ff9f0a);
  color: #0b0b0b;
  border-color: #0b0b0b;
  box-shadow: 0 4px 0 #5a0f14;
}

.btn-ghost {
  background: rgba(0, 0, 0, 0.3);
  color: #f2f2f2;
  border-color: #f2f2f2;
  box-shadow: none;
}

.page {
  display: flex;
  flex-direction: column;
  gap: 64px;
}

.hero {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 36px;
  padding: 40px;
  border: 2px solid #e23b44;
  border-radius: 16px;
  background: linear-gradient(135deg, rgba(226, 59, 68, 0.16), rgba(0, 240, 255, 0.08));
  overflow: hidden;
}

.scanline {
  position: absolute;
  inset: 0;
  background-image: linear-gradient(rgba(255, 255, 255, 0.04) 50%, transparent 50%);
  background-size: 2px 2px;
  pointer-events: none;
}

.hero__content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.hero__eyebrow {
  color: #00f0ff;
  letter-spacing: 1px;
  font-size: 11px;
}

.hero__title {
  font-size: clamp(28px, 5vw, 42px);
  color: #f8e71c;
  text-shadow: 0 0 10px rgba(248, 231, 28, 0.5);
}

.hero__subtitle {
  line-height: 1.6;
  color: #dfe6e9;
}

.hero__cta {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.hero__meme {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.crt-card {
  border: 2px solid #00f0ff;
  border-radius: 14px;
  padding: 22px 18px 22px 18px;
  background: rgba(0, 0, 0, 0.45);
  box-shadow: 0 0 0 2px #0b0b0b, 0 10px 0 rgba(0, 240, 255, 0.2);
  width: min(320px, 100%);
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: flex-start;
}

.pixel-badge {
  display: inline-block;
  background: #e23b44;
  color: #0b0b0b;
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 11px;
  box-shadow: 0 3px 0 #951d26;
}

.crt-card .small {
  font-size: 12px;
  color: #9bdaf0;
}

.blink {
  animation: blink 1s steps(2, start) infinite;
  color: #00f0ff;
  margin-top: 8px;
}

@keyframes blink {
  to {
    visibility: hidden;
  }
}

.grid {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.section-title {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.section-lead {
  color: #cfd8dc;
  line-height: 1.6;
}

.eyebrow {
  color: #00f0ff;
  letter-spacing: 1px;
  font-size: 11px;
  margin: 0;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
}

.card {
  border: 2px solid #2d323d;
  border-radius: 14px;
  padding: 22px;
  background: rgba(20, 24, 33, 0.88);
  box-shadow: 0 8px 0 #0b0b0b;
}

.card--accent {
  border-color: #e23b44;
  box-shadow: 0 6px 0 #951d26;
}

.card h3,
.card h2 {
  margin-top: 0;
  color: #f8e71c;
}

.card p {
  color: #dfe6e9;
  line-height: 1.5;
}

.list {
  margin: 12px 0 0;
  padding-left: 18px;
  color: #cfd8dc;
  line-height: 1.5;
}

.list.inline {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding-left: 0;
}

.list.inline li {
  list-style: none;
  background: #1f2430;
  padding: 6px 10px;
  border-radius: 6px;
  border: 1px solid #2d323d;
}

.list.steps {
  margin-left: 18px;
}

.cta-row {
  display: flex;
  gap: 14px;
  flex-wrap: wrap;
}

.two-col {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
}

.highlight {
  background: linear-gradient(135deg, rgba(0, 240, 255, 0.12), rgba(226, 59, 68, 0.14));
  border-color: #00f0ff;
  box-shadow: 0 6px 0 #0b0b0b, 0 0 0 2px rgba(0, 240, 255, 0.25);
}

.service-card {
  background-image: linear-gradient(45deg, rgba(226, 59, 68, 0.08), rgba(0, 240, 255, 0.05));
}

.footer {
  max-width: 900px;
  margin: 0 auto;
  padding: 48px 28px 64px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 48px;
  font-size: 13px;
  line-height: 1.6;
  border-top: 2px solid #2d323d;
  color: #dfe6e9;
  text-align: center;
}

@media (max-width: 780px) {
  .footer {
    flex-direction: column;
    align-items: center;
    gap: 32px;
    padding: 40px 12px 48px;
    max-width: 100vw;
  }
  .footer__col {
    width: 100%;
    max-width: 340px;
    margin: 0 auto;
    align-items: center;
    text-align: center;
  }
}

.footer__col {
  display: flex;
  flex-direction: column;
  gap: 12px;
  min-width: 0;
  align-items: center;
  flex: 1;
}

.footer__link {
  color: #f8e71c;
  text-decoration: none;
}

.footer__links {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.footer__links a {
  color: #cfd8dc;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  padding: 6px 10px;
  border: 1px solid #2d323d;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.04);
}

.footer__copy {
  color: #9ea7ad;
  font-size: 12px;
  line-height: 1.6;
  margin-top: 16px;
}

@media (max-width: 780px) {
  .nav {
    position: absolute;
    right: 24px;
    top: 68px;
    flex-direction: column;
    align-items: flex-start;
    background: #0b0b0b;
    border: 2px solid #2d323d;
    border-radius: 10px;
    padding: 12px;
    display: none;
  }

  .nav.is-open {
    display: flex;
  }

  .nav-toggle {
    display: flex;
  }

  .main {
    padding-top: 84px;
  }
}
