/* Apply Google policy-like Latin font (Roboto) for privacy policy page */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

.privacy-policy-page {
  padding-bottom: 48px;
}

.privacy-policy-section {
  margin-bottom: 32px;
}

.privacy-policy-heading {
  font-family: 'Roboto', 'Noto Sans JP', system-ui, -apple-system, sans-serif;
  font-weight: 500;
  letter-spacing: 0.01em;
}

.privacy-policy-text {
  font-family: 'Noto Sans JP', 'Roboto', system-ui, -apple-system, sans-serif;
}

.privacy-policy-latin {
  font-family: 'Roboto', 'Noto Sans JP', system-ui, -apple-system, sans-serif;
  font-weight: 400;
  letter-spacing: 0.01em;
}

.privacy-policy-list {
  margin-top: 8px;
  margin-bottom: 8px;
}

@media (max-width: 780px) {
  .privacy-policy-page {
    padding-bottom: 24px;
  }
  .privacy-policy-section {
    margin-bottom: 20px;
  }
}
